import { Injectable } from '@angular/core';
import {AngularFireDatabase} from "angularfire2/database";
import {MessagingProvider} from "../messaging/messaging";
import {Observable} from "rxjs/Observable";
import {CRSTeam} from "../../models/CRSTeam";

/*
  Generated class for the TeamsProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class TeamsProvider {
  teams: Array<CRSTeam>;

  constructor(private afDatabase: AngularFireDatabase, private messagingService: MessagingProvider) {

  }

  loadTeams() {
    return Observable.create(observer => {
      if(this.teams){
        observer.next(this.teams);
      }else {
        let teamsURI = "teams/";
        this.afDatabase.database.ref().child(teamsURI).once('value').then((snapshot) => {
          let t = snapshot.val();
          if(t){
            this.teams = t;
            observer.next(this.teams);
          }else {
            this.messagingService.toast("Unable to load teams. Try Again.",false);
            observer.error();
          }
        });
      }
    });
  }

}



